import React, { useState } from "react";
import "./styles.css";

const emojiDictionary = {
  "üòÇ": "Laughing",
  "üëã": "Waving Hand",
  "üòû": "Dissappointed Face",
  "üòè": "Smirking Face",
  "üòé": "Chilling Face",
  "üëå": "OK Hand",
  "ü§û": "Crossed Fingers",
  "ü§ü": "Love-You Gesture",
  "‚úä": "Raised Fist",
  "üëä": "Fist Bumps",
  "üëè": "Clapping Hands",
  "üôå": "Raising Hands"
};

let emojis = Object.keys(emojiDictionary);

export default function App() {
  const [meaning, setMeaning] = useState("Click or Type an Emoji");

  function inputChangeHandler(event) {
    var userInput = event.target.value;
    var meaning = emojiDictionary[userInput];

    if (meaning in emojiDictionary) {
      meaning = "OOps!! This emoji's not exist in our databse";
    }
    setMeaning(meaning);
  }

  function changeHandler(emoji) {
    var meaning = emojiDictionary[emoji];
    setMeaning(meaning);
  }

  return (
    <div className="App">
      <h1>Emoji Pedia </h1>
      <input onChange={inputChangeHandler} />
      <h2> {meaning} </h2>
      <h2> Emoji's We Know </h2>

      <div id="signs" style={{ color: "#1F2937" }}>
        {emojis.map((emoji) => {
          return (
            <span onClick={() => changeHandler(emoji)} key={emoji}>
              {emoji}
            </span>
          );
        })}
      </div>
    </div>
  );
}
